import { Ref } from "vue";

export interface Position {
  lat: number;
  lng: number;
}

export enum PartnerType {
  FACTORY = "factory",
  FARM = "farm",
  TRADER = "trader",
}

export interface Marker {
  position: Position;
  title: string;
  info?: { label: string; text: string }[];
  type?: string;
}

export interface Parameters {
  coordinates?: { lat: number; lng: number };
  zoom?: number;
  markers?: Marker[];
}

export enum AddressComponentTypesEnum {
  street_number = "street_number",
  route = "route",
  political = "political",
  sublocality = "sublocality",
  sublocality_level_1 = "sublocality_level_1",
  locality = "locality",
  administrative_area_level_3 = "administrative_area_level_3",
  administrative_area_level_2 = "administrative_area_level_2",
  administrative_area_level_1 = "administrative_area_level_1",
  country = "country",
  postal_code = "postal_code",
}

export type AddressComponent = {
  long_name: string;
  short_name: string;
  types: AddressComponentTypesEnum[];
};

export type GeocodeResultItem = {
  address_components: AddressComponent[];
  formatted_address: string;
  geometry: {
    location: {
      lat: () => number;
      lng: () => number;
    };
  };
  types: AddressComponentTypesEnum[];
};

export type GeocoderResult = {
  results: GeocodeResultItem[];
};

export interface Geocoder {
  geocode(request: GeocoderRequest): Promise<GeocoderResult | undefined>;
}

type AutocompleteResponse = {
  predictions: Record<string, any>;
};

export interface AutocompleteService {
  getPlacePredictions(
    request: AutocompletionRequest
  ): Promise<AutocompleteResponse | undefined>;
}

export interface GeocoderRequest {
  address?: string;
  [key: string]: any;
}

export interface AutocompletionRequest {
  input?: string;
  [key: string]: any;
}

interface GeocoderConstructor {
  new (): Geocoder;
}

interface AutocompleteServiceConstructor {
  new (): AutocompleteService;
}

export interface MapApiInterface {
  Geocoder: GeocoderConstructor;
  places: {
    AutocompleteService: AutocompleteServiceConstructor;
  };
  LatLngBounds: any;
}

export interface MapPosition {
  lat: number;
  lng: number;
}

export interface Map {
  api: MapApiInterface;
  ready: boolean;
  map: {
    panTo: (position: MapPosition) => void;
    setZoom: (zoom: number) => void;
    getBounds: () => {
      extend: (position: MapPosition) => void;
      getCenter: () => MapPosition;
    };
    getZoom: () => number | undefined;
    fitBounds: (param: any) => void;
    panToBounds: (param: any) => void;
  };
}

export type FindByAddressResult = MapPosition & {
  formatted_address: string;
  address_components: AddressComponent[];
};
export type FindByAddressType = (
  address: string
) => Promise<FindByAddressResult | undefined>;

export interface LacMapComponentInterface {
  findAddress: FindByAddressType;
  findByLatLng: (
    lat: number,
    lng: number
  ) => Promise<FindByAddressResult | undefined>;
  setCenter(lat: number, lng: number): void;
  ready: Ref<boolean>;
  geocoder: Ref<Geocoder | null>;
  fetchPlaces: (str: string) => Promise<string[]>;
  panTo: (position: MapPosition) => void;
  panToMultiple: (positions: MapPosition[]) => void;
  setZoom: (zoom: number) => void;
}
